      <div class="title">全球首家NFT早鸟投融资孵化平台</div>
      <div class="time-box">
        <div class="time-title">NftList系统参投结束倒计时...</div>
        <index-count-down
          :endDate="time"
          @timeUp="timeEnd"
          :type="4"
          :theme="2"
          :timeUnit="['天','时','分','秒']"
        />
        <!-- <van-count-down :time="time" format="HH:MM:SS">
          <template #default="timeData">
            <div class="time-list flex-center">
              <div class="time-item">
                <div class="block">{{ timeData.days }}</div>
                <div class="colon">天</div>
              </div>
              <img src="@/assets/images/new/mao@2x.png" alt="">
              <div class="time-item">
                <div class="block">{{ timeData.hours }}</div>
                <div class="colon">时</div>
              </div>
              <img src="@/assets/images/new/mao@2x.png" alt="">
              <div class="time-item">
                <div class="block">{{ timeData.minutes }}</div>
                <div class="colon">分</div>
              </div>
              <img src="@/assets/images/new/mao@2x.png" alt="">
              <div class="time-item">
                <div class="block">{{ timeData.seconds }}</div>
                <div class="colon">秒</div>
              </div>
            </div>
          </template>
        </van-count-down> -->
      </div>
      <div class="btn-box">
        <div class="btn-title">
          <p>拔得头筹  限量抢购</p>
          <p>NfltList Fund 独家预售</p>
        </div>
        <div class="btn btn-can" @click="$router.push({name: 'Throw'})">立 即 参 投</div>
        <div class="btn btn-shuo" @click="showPopup">NftList参投告知说明</div>
      </div> 
    </div>
    <div class="bottom-box">
      <img src="@/assets/images/new/index1.png" alt="">
      <img src="@/assets/images/new/index2.png" alt="">
      <img src="@/assets/images/new/index3.png" alt="">
      <img src="@/assets/images/new/index4.png" alt="">
